@import '../../../../style/variable.scss';

.gymadress {
  height: 100%;
  width: 100%;
  padding: 25px 0;
  grid-row: 2/3;
  &__wrap {
    width: 90%;
    display: grid;
    grid-template: 1fr / 1fr 20px 1fr 20px 1fr 20px 1fr;
    align-items: start;
  }

  &__item_one {
    grid-column: 1/2;
  }
  &__item_two {
    grid-column: 3/4;
  }
  &__item_three {
    grid-column: 5/6;
  }
  &__item_four {
    grid-column: 7/8;
  }

  &__tittle {
    p {
      color: rgb(155, 155, 155);
      letter-spacing: 1px;
    }
  }
  &__block {
    padding-top: 15px;
    color: var(--searchBlock);
    display: flex;
    align-items: center;
  }
  &__block_open {
    padding-top: 15px;
  }
  &__icon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }

  svg {
    fill: var(--oldRed);
    width: 100%;
    height: 100%;
  }

  &__text {
    p {
      letter-spacing: 1px;
    }
  }

  &__open {
    width: 100px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--oldRed);
    p {
      color: #fff;
    }
  }
  @media (max-width: $mw2 + px) {
    &__wrap {
      width: 100%;
    }
  }
  @media (max-width: $mw3 + px) {
    &__column {
      padding-top: 0px;
    }
    &__wrap {
      grid-template: 1fr 20px 1fr / 1fr 20px 1fr !important;
    }
    &__item_one {
      grid-row: 1;
      grid-column: 1/2;
    }
    &__item_two {
      grid-row: 1;
      grid-column: 3/4;
    }
    &__item_three {
      grid-row: 2;
      grid-column: 1/2;
    }
    &__item_four {
      grid-row: 2;
      grid-column: 3/4;
    }
  }
  @media (max-width: $mw5 + px) {
    & {
      padding: 15px 0;
    }
    &__block {
      padding-top: 10px;
    }
    &__open {
      width: 80px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: var(--oldRed);
    }
    &__wrap {
      column-gap: 0px;
      grid-template: auto 5px auto 5px auto 5px auto/ 1fr !important;
      height: 100%;
    }
    &__item_one {
      grid-row: 1/2;
      grid-column: 1;
    }
    &__item_two {
      grid-row: 3/4;
      grid-column: 1;
    }
    &__item_three {
      grid-row: 5/6;
      grid-column: 1;
    }
    &__item_four {
      grid-row: 7/8;
      grid-column: 1;
    }
    &__icon {
      width: 15px;
      height: 15px;
      margin-right: 10px;
    }
  }
}
